<div class="bg" :style="'background-image: url(' + category.background + ')'"><Modal v-model:active="visible_setting"><CategorySetting></CategorySetting></Modal><Modal v-model:active="visible_fucus_question" :width="640" scroll="keep"><div class="hero"><div class="hero-body"><div class="title first">Question number {{focus_idx + 1}}</div><div class="title second">{{focus_question.message}}</div></div></div></Modal><div class="title">{{category.title}}</div><div class="is-inline"><div class="button is-small" @click="visible_setting = true" style="top: 1.5rem; position: absolute; left: 1rem; background-color: transparent; opacity: 0.5"><label style="color: white">setting</label></div></div><div style="padding: 0 1rem 1rem 1rem"><div id="category-grid" :style="grid_style"><div class="flip-wrapper" v-for="(q, i) in category.questions" :key="i + q.message"><FlipVue :ref="el =&gt; pre_open_flipcard(i, el)" :active-hover="false" :active-click="true" width="100%" height="100%" transition="1s"><template v-slot:front><div class="box plate" @click="flip(i, true)"><h1 style="font-size: 3rem; margin: 0 auto">{{i+1}}</h1></div></template><template v-slot:back><div class="box plate is-close my-flex" @click="flip(i, false)"><div style="text-align: center;">{{q.message}}</div></div></template></FlipVue></div></div></div></div>