<div class="modal-card" style="margin: auto;"><div class="modal-card-head"><p class="modal-card-title">ตั้งค่า</p></div><div class="modal-card-body"><section><div class="field"><div class="control"><div class="field has-addons"><p class="control"><a class="button is-static" style="width: 140px">title</a></p><p class="control"><input class="input" type="text" v-model="state.title"></p></div></div></div><div class="field"><div class="control"><div class="field has-addons"><p class="control"><a class="button is-static" style="width: 140px">num row</a></p><p class="control"><input class="input" type="number" v-model="state.nrow"></p></div></div></div><div class="field"><div class="control"><div class="field has-addons"><p class="control"><a class="button is-static" style="width: 140px">num col</a></p><p class="control"><input class="input" type="number" v-model="state.ncol"></p></div></div></div><div class="field"><div class="control"><div class="field has-addons"><p class="control"><a class="button is-static" style="width: 140px">background</a></p><p class="control"><input class="input" type="text" v-model="state.background"></p></div></div></div><div class="field"><div class="control"><textarea class="textarea" :class="{'is-success': valid, 'is-danger': !valid}" v-model="questions"></textarea></div></div></section><div class="field"><div class="control"><a class="is-danger" @click="clear_text_questions">clear storage [ลบข้อมูล !!!]</a></div></div></div></div>